import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTolakanReturnSupplier } from "../actions/returnSupplier";
import { Field, reset } from "redux-form";

import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import { finishSend, progressSend } from "../actions/LogicAction";
import { AxiosMasterPost } from "../Axios";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { getsupplierList } from "../actions/supplierAction";
import { renderField, renderFieldSelect } from "../renderField";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { multipleDeleteLocal } from "../helper";
const FormTerimaSupplier = lazy(() => import("./Component/FormTerimaSupplier"));

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    returnSupplier: state.provinsi.listTolakanReturnSupplier,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
    supplier: state.provinsi.supplier,
  };
};

class TolakanReturnSupplier extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      supplierList: [],
      DataSupplier: [],
      startDate: formatedDate,
      kodeSupplier: "",
      namaSupplier: "",
      tanggal: formatedDate,
      noBon: "",
      status: "",
      tampungBarang: [],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadar_beli",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos(RP)",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "TolakanReturnSupplier",
                      rowIndex,
                      this.props.dispatch,
                      getListTolakanReturnSupplier()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getListTolakanReturnSupplier());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    var supp = data.kodeJenis
      ? data.kodeJenis
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = {
      kode_kategori: data.kode_kategori,
      kode_jenis: split[0],
      kadar_cetak: parseFloat(data.kadar),
      kadar_beli: parseFloat(data.kadarTransaksi),
      no_barcode: "-",
      qty: 1,
      p: parseFloat(data.p),
      k: parseFloat(data.k),
      pkg: parseFloat(data.pkg),
      gross: parseFloat(data.gross),
      bruto: parseFloat(data.bruto),
      netto: parseFloat(data.netto),
      ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp),
    };

    var array = JSON.parse(localStorage.getItem("TolakanReturnSupplier")) || [];
    array.push(simpan);
    localStorage.setItem("TolakanReturnSupplier", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListTolakanReturnSupplier());
    this.props.dispatch(reset("TerimaSupplier"));
  }

  returnSupplierSubmit() {
    if (this.state.noBon === "") {
      warning("Mohon isi nomor bon");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi Tanggal");
      return false;
    } else if (localStorage.getItem("TolakanReturnSupplier") === null) {
      warning("Barang masih kosong, silahkan tambahkan barang");
      return false;
    } else if (this.state.kodeSupplier === "") {
      warning("Mohon pilih Supplier");
      return false;
    }
    let data = JSON.parse(localStorage.getItem("TolakanReturnSupplier"));
    let qty = data.length;
    let p = data.map((list) => parseFloat(list.p)).reduce((a, b) => a + b, 0);
    let k = data.map((list) => parseFloat(list.k)).reduce((a, b) => a + b, 0);
    let pkg = data
      .map((list) => parseFloat(list.pkg))
      .reduce((a, b) => a + b, 0);
    let bruto = data
      .map((list) => parseFloat(list.bruto))
      .reduce((a, b) => a + b, 0);
    let netto = data
      .map((list) => parseFloat(list.netto))
      .reduce((a, b) => a + b, 0);
    let ongkos_rp = data
      .map((list) => parseFloat(list.ongkos_rp))
      .reduce((a, b) => a + b, 0);
    let isi = {
      no_tolak: this.props.noFaktur,
      tanggal: this.state.tanggal,
      no_bon: this.state.noBon,
      kode_supplier: this.state.kodeSupplier,
      jenis_trx: "RETUR",
      total_qty: qty,
      total_p: p,
      total_k: k,
      total_pkg: pkg,
      total_bruto: bruto,
      total_netto: netto,
      total_ongkos: ongkos_rp,
      detail_barang: JSON.parse(localStorage.getItem("TolakanReturnSupplier")),
    };
    console.log(JSON.stringify(isi));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("TolakanReturnSupplier"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        ticket.kadar_cetak,
        ticket.p,
        ticket.k,
        ticket.pkg,
        ticket.bruto,
        ticket.kadar_beli,
        parseFloat(ticket.netto).toFixed(3),
        "Rp. " + parseFloat(ticket.ongkos_rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      "",
      table.map((list) => list.pkg).reduce((a, b) => a + b, 0),
      table.map((list) => list.bruto).reduce((a, b) => a + b, 0),
      "",
      table.map((list) => list.netto).reduce((a, b) => a + b, 0),
      "Rp. " +
        table
          .map((list) => list.ongkos_rp)
          .reduce((a, b) => a + b, 0)
          .toLocaleString("id-ID"),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Barang",
      "Kadar Cetak (%)",
      "P",
      "K",
      "Bungkus(Gr)",
      "Berat(Gr)",
      "Harga(Gr)",
      "Murni(Gr)",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("trx-tolakan-supplier/simpan", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Tanggal",
              this.state.tanggal,
              "Supplier",
              this.state.kodeSupplier.toUpperCase(),
              "",
              "",
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Tolakan Return Barang Supplier",
              tableRows
            )
          )
          .then(() =>
            multipleDeleteLocal(["TolakanReturnSupplier", "noFaktur"])
          )
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListTolakanReturnSupplier()))
          .then(() =>
            this.setState({
              noBon: "",
              kodeSupplier: "",
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Tolakan Return Supplier" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    <Suspense
                      fallback={<Skeleton width={"100%"} height={400} />}
                    >
                      <FormTerimaSupplier
                        from="tolakan"
                        onSubmit={(data) => this.handleSubmit(data)}
                        noFaktur={this.props.noFaktur}
                      />
                    </Suspense>
                  }
                  title="Tambah Data Barang"
                />
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-12">
                      <form id="myForm">
                        <div className="row">
                          <div className="col-lg-4">
                            <Field
                              name="supplier"
                              component={renderFieldSelect}
                              options={this.props.supplier.map((list) => {
                                let data = {
                                  value: list.kode_supplier,
                                  name: list.nama_supplier,
                                };
                                return data;
                              })}
                              type="text"
                              label="Supplier"
                              placeholder="Masukan Supplier"
                              onChange={(data) => {
                                this.setState({
                                  kodeSupplier: data,
                                });
                              }}
                            />
                          </div>
                          <div className="col-lg-4">
                            <Field
                              name="tanggal"
                              component={renderField}
                              type="date"
                              label="Tanggal"
                              placeholder="Masukan Tanggal"
                              onChange={(e) => {
                                this.setState({
                                  tanggal: e.target.value,
                                });
                              }}
                            />
                          </div>
                          <div className="col-lg-4">
                            <Field
                              name="no_bon"
                              component={renderField}
                              type="text"
                              label="Nomor Bon"
                              placeholder="Masukan Nomor Bon"
                              onChange={(data) => {
                                this.setState({
                                  noBon: data.target.value,
                                });
                              }}
                            />
                          </div>
                        </div>
                      </form>
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="row">
                        <div className="col-lg-6"></div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.props.dispatch(showModal())}
                            >
                              Tambah Data
                              <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField="id"
                        data={this.props.returnSupplier}
                        columns={this.state.columns}
                      />
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          disabled={this.props.onSend}
                          onClick={() => this.returnSupplierSubmit()}
                        >
                          {this.props.onSend ? (
                            <span
                              className="spinner-border spinner-border-sm mr-3"
                              aria-hidden="true"
                            ></span>
                          ) : null}
                          Simpan
                          <i
                            class="fa fa-paper-plane ml-3"
                            aria-hidden="true"
                          ></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TolakanReturnSupplier);
